"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import contentpt from "@/assets/contentpt.json";
import contenten from "@/assets/contenten.json";
import { useLanguage } from "./LanguageContext";
import LanguageSelection from "./LanguageSelection";

export default function PageSelection() {
  const { language } = useLanguage();
  const pathname = usePathname();
  const content = language === "pt" ? contentpt : contenten;

  return (
    <>
      <div className="absolute top-17 inset-0 bg-[var(--blur)] backdrop-blur-[1.5px]"></div>

      <ul className="absolute right-0 top-17 bg-[var(--gray)] w-80 justify-start items-end px-10 py-5 h-screen flex flex-col gap-4 opacity-90">
        {content.header.links.map((item) => (
          <li key={item.url}>
            <Link
              href={item.url}
              className={`px-2 py-1 inline-flex items-center justify-center rounded-full text-center ${
                pathname === item.url ? "bg-white shadow-md" : ""
              }`}
            >
              {item.label}
            </Link>
          </li>
        ))}
      </ul>
    </>
  );
}
